.header_wrapper {
  margin-bottom: auto;
  padding: 10px;
  text-align: center;
  width: 100%;
  margin: 20px auto 0 auto;
  max-width: 1000px;
}

.header_title {
  font-weight: 300;
}

.header_text {
  padding: 20px;
  font-size: 1.2rem;
}
.header_links {
  display: flex;
  justify-content: space-evenly;
  padding: 5px;
  margin-top: 25px;
}

.card_icon {
  padding: 1rem;
  font-size: 1.5rem;
}

@media (max-width: 425px) {
  .header_links {
    flex-direction: column;
    padding: 10px;
  }

  .header_links > a {
    padding: 5px;
  }
}
